import React from 'react';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';
import { graphql, useStaticQuery } from "gatsby";
import Img from "gatsby-image"

// Styles
import "../../../node_modules/react-responsive-carousel/lib/styles/carousel.min.css";
import "./Slider.styles.scss";
 
const Slider = () => {
    
    const data = useStaticQuery(graphql`
        query {
            allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/(testimonials)/"}, frontmatter: {title: {ne: "Testimonials"}}}, limit: 2, sort: {fields: id, order: DESC}) {
                edges {
                    node {
                        frontmatter {
                            title
                            testimonialIntro
                            jobTitle
                            image
                            productImage {
                                childImageSharp {
                                    fluid (maxWidth: 1920) {
                                        ...GatsbyImageSharpFluid
                                    }
                                }
                            }
                        }
                        html
                    }
                }
            }
        }
    `)
    
    return (
        <Carousel showIndicators={false} showThumbs={false} showStatus={false} infiniteLoop>
            {
                data.allMarkdownRemark.edges.map(({node}) => (
                    <div className="testimonials-container">
                        <Img fluid={ node.frontmatter.productImage.childImageSharp.fluid } />
                        <div className="testimonials-content">
                            <h3><em>{ node.frontmatter.testimonialIntro }</em></h3>
                            <h4>{ node.frontmatter.title }</h4>
                            <p className="sub-head">{ node.frontmatter.jobTitle }</p>
                            <div dangerouslySetInnerHTML={{ __html: node.html }} />
                        </div>
                    </div>
                ))
            }
        </Carousel>
    )
};
 
// ReactDOM.render(<Slider />, document.querySelector('.demo-carousel'));
export default Slider;